:root {
  --font-color: #736c1a;
  --primary-color: #332030;
  --top-line-color: #918840;
  --logo-line-color: #000;
  --logo-title-color: #aba018;
  --intro-section-primary-text-color: var(--primary-color);
  --intro-section-secondary-text-color: #bdad3c;
  --project-list-link-color: #756c26;
  --project-list-link-hover-color: #d4c239;
  --btn-primary-color: #867b28;
  --btn-primary-hover-color: #bfb148;
  --header-background-color: #f9f9f9;
  --header-border-color: #d4d4d4;
  --foter-text-color: #fff;
  --footer-background-color: #332a20;
  --github-hover-color: #a79a23;
}

body {
  margin: 0;
  padding-top: 72px; //fixed header
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 16px;
  line-height: 1.35;
  color: #3e1927;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h2, h3, h4, h5, span, p {
  font-family: 'Roboto', sans-serif;
  color: var(--font-color);
}

*,
*:after,
*::before {
  box-sizing: border-box;
}

.app {
  overflow-x: hidden;
  height: calc(100vh - 72px);
}